import React from 'react';
import { Table, Form } from 'react-bootstrap';
import './individuals-mana.scss';
import adminApi from '../../../services/admin.service';
import helper from '../../../services/helper.service';

const img_default_individual = require('../../../assets/profile-images/default-individual.png');

class IndividualsMana extends React.Component {
    constructor(props) {
        super();
        this.state = {
            individuals: [],
        }
    }

    componentDidMount() {
        adminApi.getIndividuals()
            .then((response) => {
                this.setState({individuals: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
            })
    }

    handlePermission = (event, id) => {
        const permission = event.target.value;
        adminApi.updateUserPemission({id: id, permission: permission, role: 'individual'})
            .then((response) => {
                this.setState({individuals: response.data});
                helper.showToast('Success', 'Successfully updated.', 1);
            })
            .catch((error) => {
                console.log('err: ', error);
                helper.showToast('Error', 'Error occupied.', 3);
            });
    }

    render() {
        return (
            <div className="inividuals-mana">
                <div className="card box-shadow">
                    <Table responsive striped bordered hover>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th className="name"> اسم المستخدم </th>
                                <th className="email"> البريد الإلكتروني </th>
                                <th className="avatar"> الصورة الرمزية لل </th>
                                <th className="gender"> جنس </th>
                                <th className="address"> عنوان </th>
                                <th className="city"> مدينة </th>
                                <th className="state"> حالة </th>
                                <th className="phone"> هاتف </th>
                                <th className="hourly rate"> المعدل بالساعة </th>
                                <th className="registered_by"> مسجل من قبل </th>
                                <th className="permission"> الإذن </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.individuals && this.state.individuals.map((obj, index) => 
                                <tr key={index}>
                                    <td> {obj.id} </td>
                                    <td> {obj.name} </td>
                                    <td> {obj.email} </td>
                                    <td> 
                                        <img src={obj.logo ? obj.logo : img_default_individual} alt={obj.name} className="img-logo" />
                                    </td>
                                    <td> {obj.gender} </td>
                                    <td> {obj.address} </td>
                                    <td> {obj.city} </td>
                                    <td> {obj.state} </td>
                                    <td> {obj.phone} </td>
                                    <td> {obj.hourly_rate} </td>
                                    <td> {obj.registered_by} </td>
                                    <td> 
                                    <Form.Control as="select" onChange={(event) => this.handlePermission(event, obj.id)}>
                                        <option value="active" selcted={obj.permission === 'active' ? 'selected' : ''}> active </option>
                                        <option value="pending" selcted={(obj.permission === 'pending' || obj.permission === '') ? 'selected' : ''}> pending </option>
                                        <option value="close" selcted={obj.permission === 'close' ? 'selected' : ''}> closed </option>
                                    </Form.Control>
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </Table>
                </div>
            </div>
        );
    }
}

export default IndividualsMana;