import React from 'react';

import './jobs.scss';
import ForasHeroImage from '../../../components/ForasHeroImage';
import ForasFilterJobs from '../../../components/ForasFilterJobs';
import jobApi from '../../../services/job.service';

const hero_img = require('../../../assets/images/map-hero.png');
const img_map_footer = require('../../../assets/images/map-footer.png');

class ForasJobs extends React.Component {
    constructor(props) {
        super();
        this.state = {
            activeSort: null,
            jobs: [],
        }
    }

    componentDidMount() {
        jobApi.getJobs()
            .then((response) => {
                this.setState({jobs: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
            })
    }

    getJobs = () => {
        jobApi.getJobs({sort: this.state.activeSort})
            .then((response) => {
                this.setState({jobs: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
            })
    }

    handleSort = (sort) => {
        this.setState({activeSort: this.state.activeSort === sort ? null : sort});
        this.getJobs();
    }

    render() {
        return (
            <div className="main">
                <ForasHeroImage src={hero_img} footer={false} />
                <img src={img_map_footer} className="map-footer" alt="map-footer" />
                <ForasFilterJobs data={this.state.jobs} activeSort={this.state.activeSort} handleSort={this.handleSort} />
            </div>
        );
    }
}

export default ForasJobs;