import React from 'react';
import jobApi from '../../services/job.service';
import './map.scss';
import ForasHeroImage from '../../components/ForasHeroImage';
import ForasOpportunityMap from '../../components/ForasOpportunityMap';
import ForasFilterJobs from '../../components/ForasFilterJobs';
import ForasFilterIndividuals from '../../components/ForasFilterIndividuals';
import session from '../../services/session.service';
import userApi from '../../services/user.service';

const hero_img = require('../../assets/images/map-hero.png');
const img_map_footer = require('../../assets/images/map-footer.png');

class Map extends React.Component {
    constructor(props) {
        super();
        this.state = {
            activeSort: null,
            jobs: [],
            individuals: [],
            userRole: null,
        }
    }

    componentDidMount() {
        const user = session.get('foras-user');
        if(user) {
            this.setState({userRole: user.role});
            // if(user.role === 'individual') {
            //     this.getJobs();
            // } else {
            //     this.getIndividuals();
            // }
        }
        this.getJobs();
        this.getIndividuals();
    }

    getJobs = () => {
        jobApi.getJobs({sort: this.state.activeSort})
            .then((response) => {
                this.setState({jobs: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
            })
    }

    getIndividuals = () => {
        userApi.getIndividuals({sort: this.state.activeSort})
            .then((response) => {
                this.setState({individuals: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
            })
    }

    handleSort = (sort) => {
        this.setState({activeSort: this.state.activeSort === sort ? null : sort});
        console.log('>>', this.state.userRole);
        this.getJobs();
    }

    render() {
        return (
            <div className="main">
                <ForasHeroImage src={hero_img} footer={false} />
                {/* {this.state.userRole && this.state.userRole === 'individual' 
                    ? <ForasOpportunityMap data={this.state.jobs} userRole={this.state.userRole} /> 
                    : <ForasOpportunityMap data={this.state.individuals} userRole={this.state.userRole} />} */}
                <ForasOpportunityMap data={this.state.jobs} userRole={this.state.userRole} />
                <img src={img_map_footer} className="map-footer" alt="map-footer" />
                {/* {this.state.userRole && this.state.userRole === 'individual' 
                    ? <ForasFilterJobs data={this.state.jobs} activeSort={this.state.activeSort} handleSort={this.handleSort} /> 
                    : <ForasFilterIndividuals data={this.state.individuals} activeSort={this.state.activeSort} handleSort={this.handleSort} />} */}
                <ForasFilterJobs data={this.state.jobs} activeSort={this.state.activeSort} handleSort={this.handleSort} />
            </div>
        );
    }
}

export default Map;