import React from 'react';
import { Table } from 'react-bootstrap';
import './messages-mana.scss';
import adminApi from '../../../services/admin.service';
import helper from '../../../services/helper.service';

class MessagesMana extends React.Component {
    constructor(props) {
        super();
        this.state = {
            messages: [],
        }
    }

    componentDidMount() {
        adminApi.getMessages()
            .then((response) => {
                this.setState({messages: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
                helper.showToast('Error', 'Error occupied.', 3);
            })
    }


    render() {
        return (
            <div className="messages-mana">
                <div className="card box-shadow">
                    <Table responsive striped bordered hover>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th className="sender"> رسالة </th>
                                <th className="receiver"> صنع من قبل </th>
                                <th className="subject"> صنع من قبل </th>
                                <th className="message"> أنشئت في </th>
                                <th className="created_at"> أنشئت في </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.messages && this.state.messages.map((obj, index) => 
                                <tr key={index}>
                                    <td> {obj.id} </td>
                                    <td> {obj.sender.name} </td>
                                    <td> {obj.receiver.name} </td>
                                    <td> {obj.subject} </td>
                                    <td> {obj.message} </td>
                                    <td> {obj.created_at} </td>
                                </tr>
                            )}
                        </tbody>
                    </Table>
                </div>
            </div>
        );
    }
}

export default MessagesMana;