import React from 'react';
import { Table } from 'react-bootstrap';
import './contacts-mana.scss';
import adminApi from '../../../services/admin.service';
import helper from '../../../services/helper.service';

class ContactsMana extends React.Component {
    constructor(props) {
        super();
        this.state = {
            tickets: [],
        }
    }

    componentDidMount() {
        adminApi.getTickets()
            .then((response) => {
                this.setState({tickets: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
                helper.showToast('Error', 'Error occupied.', 3);
            })
    }

    render() {
        return (
            <div className="tickets-mana">
                <div className="card box-shadow">
                    <Table responsive striped bordered hover>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th className="name"> اسم </th>
                                <th className="email"> البريد الإلكتروني </th>
                                <th className="subject"> موضوع </th>
                                <th className="message"> رسالة </th>
                                <th className="created_at"> أنشئت في </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.tickets && this.state.tickets.map((obj, index) => 
                                <tr key={index}>
                                    <td> {obj.id} </td>
                                    <td> {obj.name} </td>
                                    <td> {obj.email} </td>
                                    <td> {obj.subject} </td>
                                    <td> {obj.message} </td>
                                    <td> {obj.created_at} </td>
                                </tr>
                            )}
                        </tbody>
                    </Table>
                </div>
            </div>
        );
    }
}

export default ContactsMana;