import React from 'react';
import { Table } from 'react-bootstrap';
import './notifications-mana.scss';
import adminApi from '../../../services/admin.service';
import AdminNewNotificationModal from '../../../components/AdminNewNotificationModal';
import helper from '../../../services/helper.service';

class NotificationsMana extends React.Component {
    constructor(props) {
        super();
        this.state = {
            notifications: [],
        }
    }

    componentDidMount() {
        adminApi.getNotifications()
            .then((response) => {
                this.setState({notifications: response.data});
            })
            .catch((error) => {
                console.log('err: ', error);
                helper.showToast('Error', 'Error occupied.', 3);
            })
    }

    render() {
        return (
            <div className="notifications-mana">
                <div className="card box-shadow">
                    <div className="action-header">
                        <AdminNewNotificationModal />
                    </div>
                    <Table responsive striped bordered hover>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th className="type"> نوع </th>
                                <th className="message"> رسالة </th>
                                <th className="created_by"> صنع من قبل </th>
                                <th className="created_at"> أنشئت في </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.notifications && this.state.notifications.map((obj, index) => 
                                <tr key={index}>
                                    <td> {obj.id} </td>
                                    <td> {obj.type} </td>
                                    <td> {obj.message} </td>
                                    <td> {obj.created_by} </td>
                                    <td> {obj.created_at} </td>
                                </tr>
                            )}
                        </tbody>
                    </Table>
                </div>
            </div>
        );
    }
}

export default NotificationsMana;